import { Field, InputType } from '@nestjs/graphql';
import { PartialType } from '@nestjs/mapped-types';
import { IsBoolean, IsNotEmpty, IsOptional, IsString, IsUUID, MinLength } from 'class-validator';
import { RelationExists } from '../../../../validation';
import { PaymentPlan } from '../../../../orm-config/entity/payment/payment-plan.entity';
import { ColumnExists } from '../../../../validation/custom/column-exists/column-exists.decorator';
import { CreatePaymentPlanInput } from './create-payment-plan.input';

@InputType()
export class UpdatePaymentPlanInput extends PartialType(CreatePaymentPlanInput) {
  
  @Field(() => String)
  @IsUUID(4)
  @RelationExists(PaymentPlan)
  id: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @IsOptional()
  @MinLength(5)
  @ColumnExists(PaymentPlan)
  name: string;

  @Field(() => Boolean, { nullable: true })
  @IsBoolean()
  @IsOptional()
  isActive: boolean;
}
